
import { defineComponent } from "vue";
import { ElMessage } from "element-plus";
import noteProcess from "../utils/dateProcess";
import WordCloud from "../utils/wordcloud";

export default defineComponent({
  data() {
    return {
      noteData: [],
    };
  },
  mounted() {
    // this.isUpload();
    this.noteData = this.$store.state.noteData;
    this.initCharts();
  },
    beforeCreate() {
    if(!this.$store.state.isLogin) {
      this.$router.push('login');
    }
  },
  methods: {
    isUpload() {
      const flag = this.$store.state.isUpload;
      if (!flag) {
        this.$router.push("Upload");
        ElMessage({
          message: "请先上传您的标注!",
          type: "error",
          center: true,
        });
      }
    },

    initCharts() {
      const bookNoteEchart = this.$echarts.init(
        document.getElementById("bookEchart")
      );
      const authorEchart = this.$echarts.init(
        document.getElementById("authorEchart")
      );
      const yearNoteNumEchart = this.$echarts.init(
        document.getElementById("noteNumEchart")
      );

      const [noteNumForPie, bookNoteNum] = noteProcess.getBookNoteNum(
        this.noteData
      );
      const [authorName, bookNum] = noteProcess.getAuthorBookNum(this.noteData);
      const yearNoteNum = noteProcess.getYearNoteNum(this.noteData);

      //书籍标注数量饼状图
      const noteNumOption = {
        tooltip: {
          trigger: "item",
        },
        series: [
          {
            name: "标注数量",
            type: "pie",
            radius: "80%",
            data: noteNumForPie,
            emphasis: {
              itemStyle: {
                shadowBlur: 10,
                shadowOffsetX: 3,
                shadowColor: "rgba(0, 0, 0, 0.5)",
              },
            },
          },
        ],
      };

      //作者柱状图
      const authorOption = {
        tooltip: {
          trigger: "axis",
          axisPointer: {
            type: "shadow",
          },
        },
        grid: {
          left: "3%",
          right: "4%",
          bottom: "3%",
          containLabel: true,
        },
        xAxis: {
          type: "value",
          boundaryGap: [0, 0.01],
        },
        yAxis: {
          type: "category",
          data: authorName,
        },
        series: [
          {
            name: "作品数量(部)",
            type: "bar",
            data: bookNum,
          },
        ],
      };

      // 词云
      var options = {
        list: bookNoteNum,
        gridSize: 1, // size of the grid in pixels
        weightFactor: 10, // number to multiply for size of each word in the list
        fontWeight: "normal", // 'normal', 'bold' or a callback
        fontFamily: "Times, serif", // font to use
        color: "random-dark", // 'random-dark' or 'random-light'
        backgroundColor: "#fff", // the color of canvas
        rotateRatio: 0, // probability for the word to rotate. 1 means always rotate
        shrinkToFit: "true",
      };
      var canvas = document.getElementById("canvas");
      WordCloud(canvas, options);

      //年度标注报告
      const yearNoteNumOption = {
        tooltip: {
          trigger: "item",
        },
        legend: {
          top: "5%",
          left: "center",
        },
        series: [
          {
            name: "年度标注数量报告",
            type: "pie",
            radius: ["40%", "70%"],
            avoidLabelOverlap: false,
            label: {
              show: false,
              position: "center",
            },
            emphasis: {
              label: {
                show: true,
                fontSize: "40",
                fontWeight: "bold",
              },
            },
            labelLine: {
              show: false,
            },
            data: yearNoteNum,
          },
        ],
      };

      authorOption && authorEchart.setOption(authorOption);
      noteNumOption && bookNoteEchart.setOption(noteNumOption);
      yearNoteNumOption && yearNoteNumEchart.setOption(yearNoteNumOption);
    },
  },
});
