/**
 * 用于对标注数据数组的处理方法：去重、重复元素数量计算等.
 */

/**
 * 
 * @param noteArray 标注数组
 * @return 含有唯一书名和作者名的集合
 */
function getUniqueName(noteArray) {
    let bookName = new Set();
    for (let item of noteArray) {
        if (item.date !== 'on' && item.note !== 'undefined') {
            bookName.add(item.name);
        }

    }
    return bookName;
}

function getAuthorBookNum(noteArray) {
    let author_= [];
    let bookNum = [];
    let authorName = [];
    const bookName = getUniqueName(noteArray);
    
    for(let name of bookName) {
        for(let item of noteArray) {
            if(name === item.name) {
                author_.push(item.author);
                break;
            }
        }
    }

    author_.sort();
    console.log(author_);
    for (let i = 0; i < author_.length;) {
        let num = 1;
        for (var j = i + 1; j < author_.length; j++) {
            if (author_[i] === author_[j]) num++;
            else break;
        }
        
        bookNum.push(num);
        authorName.push(author_[i]);
        i = j;
    }

    return [authorName,bookNum];
}

/**
 * 
 * @param noteArray 
 * @return 每本书的标注数量,返回的形式不一样,用于不同的图表显示.
 */
function getBookNoteNum(noteArray) {
    let bookName = [];
    let bookNoteNum = [];
    let noteNumForPie = [];

    for (let item of noteArray) {
        if (item.date !== 'on' && item.note !== 'undefined') {
            bookName.push(item.name);
        }
    }

    bookName.sort();
    for (let i = 0; i < bookName.length;) {
        let item = [];
        let pie = {};
        let num = 1;
        for (var j = i + 1; j < bookName.length; j++) {
            if (bookName[i] === bookName[j]) num++;
            else break;
        }
        //key必须是value & name,否则echarts不显示.
        pie['value'] = num;
        pie['name'] = bookName[i];
        item.push(bookName[i]);
        item.push(num);
        bookNoteNum.push(item);
        noteNumForPie.push(pie);
        i = j;
    }
 
    return [noteNumForPie,bookNoteNum];
}
/**
 * 
 * @param noteArray 标注数组
 * @return 每年标注数量数组
 */
function getYearNoteNum(noteArray) {
    let year = [];
    let yearNoteNum = [];

    for (let item of noteArray) {
        if (item.date !== 'on' && item.note !== 'undefined') {
            year.push(item.date.substring(0, 4));
        }
    }

    year.sort();
    for (let i = 0; i < year.length;) {
        let item = {};
        let num = 1;
        for (var j = i + 1; j < year.length; j++) {
            if (year[i] === year[j]) num++;
            else break;
        }
        //key必须是value & name,否则echarts不显示.
        item['value'] = num;
        item['name'] = year[i];
        yearNoteNum.push(item);
        i = j;
    }

    return yearNoteNum;
}

export default {
    getUniqueName,
    getYearNoteNum,
    getBookNoteNum,
    getAuthorBookNum
}